<template>
  <div>
    <div class="loader-wrapper">
      <div class="lds-ring">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
    <div class="card card-authentication1 mx-auto my-4">
      <div class="card-body">
        <div class="card-content p-2">
          <div class="text-center">
            <img src="./../../../public/assets/images/test.png" alt="logo icon">
          </div>
          <div class="card-title text-uppercase text-center py-3">Sign Up</div>
          <form @submit.prevent="register" type="post">
            <div class="form-group">
              <label for="exampleInputName" class="sr-only">Name</label>
              <div class="position-relative has-icon-right">
                <input type="text" id="exampleInputName" class="form-control input-shadow" :class="{'border border-danger': errors?.name}" placeholder="Enter Your Name" name="name" v-model="name">
                <div class="form-control-position">
                  <i class="icon-user"></i>
                </div>
              </div>
              <h6 class="font-weight-lighter text-sm text-danger" v-if="errors?.name">{{ errors?.name?.join(', ') }}</h6>
            </div>
            <div class="form-group">
              <label for="exampleInputEmailId" class="sr-only">Email ID</label>
              <div class="position-relative has-icon-right">
                <input type="email" id="exampleInputEmailId" class="form-control input-shadow" :class="{'border border-danger': errors?.email}" placeholder="Enter Your Email ID" name="email" v-model="email">
                <div class="form-control-position">
                  <i class="icon-envelope-open"></i>
                </div>
              </div>
              <h6 v-if="errors?.email" class="font-weight-lighter text-sm text-danger">{{ errors?.email?.join(', ') }}</h6>
            </div>

            <div class="form-group">
              <label for="exampleInputEmailId" class="sr-only">Phone Number</label>
              <div class="position-relative has-icon-right">
                <input type="text" id="exampleInputEmailId" class="form-control input-shadow" :class="{'border border-danger': errors?.phone}" placeholder="Phone Number" name="phone" v-model="phone">
                <div class="form-control-position">
                  <i class="icon-phone"></i>
                </div>
              </div>
              <h6 v-if="errors?.phone" class="font-weight-lighter text-sm text-danger">{{ errors?.phone?.join(', ') }}</h6>
            </div>

            <div class="form-group">
              <label for="exampleInputEmailId" class="sr-only">Image</label>
              <div class="position-relative has-icon-right">
                <input type="file" id="exampleInputEmailId" class="form-control input-shadow" :class="{'border border-danger': errors?.image}" placeholder="Image" name="image" ref="image" v-bind="image">
                <div class="form-control-position">
                  <i class="icon-picture"></i>
                </div>
              </div>
              <h6 v-if="errors?.image" class="font-weight-lighter text-sm text-danger">{{ errors?.image?.join(', ') }}</h6>
            </div>

            <div class="form-group">
              <label for="exampleInputPassword" class="sr-only">Password</label>
              <div class="position-relative has-icon-right">
                <input type="password" id="exampleInputPassword" class="form-control input-shadow" :class="{'border border-danger': errors?.password}" placeholder="Choose Password" name="password" v-model="password">
                <div class="form-control-position">
                  <i class="icon-lock"></i>
                </div>
              </div>
              <h6 v-if="errors?.password" class="font-weight-lighter text-sm text-danger">{{ errors?.password?.join(', ') }}</h6>
            </div>

            <div class="form-group">
              <label for="exampleInputPassword" class="sr-only">Password Confirmation</label>
              <div class="position-relative has-icon-right">
                <input type="password" id="exampleInputPassword" class="form-control input-shadow" placeholder="Password Confirmation" name="password_confirmation" v-model="password_confirmation">
                <div class="form-control-position">
                  <i class="icon-lock"></i>
                </div>
              </div>
            </div>

            <div class="form-group">
              <div class="icheck-material-white">
                <input type="checkbox" id="user-checkbox" checked="">
                <label for="user-checkbox">I Agree With Terms &amp; Conditions</label>
              </div>
            </div>

            <button type="submit" class="btn btn-light btn-block waves-effect waves-light">Sign Up</button>

          </form>
        </div>
      </div>
      <div class="card-footer text-center py-3">
        <p class="text-warning mb-0">Already have an account? <router-link to="login"> Sign In here</router-link></p>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "User-Register-page",
  mounted() {
    this.registerView()
  },
  data () {
    return {
      name: '',
      email: '',
      phone: '',
      image: '',
      password_confirmation: '',
      password: '',
      errors: []
    }
  },
  methods: {
    async register(data) {
      this.errors = []
      const registerRouter = this.$route?.query?.refereed_by ? (`register/${this.$route?.query?.refereed_by || ''}`) : 'register'
      await this.$axios.post(registerRouter, {
            name: data.target.name.value,
            email: data.target.email.value,
            phone: data.target.phone.value,
            image: this.$refs['image'].files[0],
            password: data.target.password.value,
            password_confirmation: data.target.password_confirmation.value
      },
          { headers: {
          "Content-Type": "multipart/form-data"
        }
          }).then(() => {
        this.resetForm()
        this.$notify({text: "Account has been created", type: 'success'})
      }).catch(err => {
        this.errors = err.response.data.errors
      })
    },
    resetForm() {
      this.name = ''
      this.email = ''
      this.phone = ''
      this.$refs['image'].value = null,
      this.password_confirmation = ''
      this.password = ''
    },
    async registerView() {
      console.log(this.$route)
      await this.$axios.get(`register/${this.$route?.query?.refereed_by || ''}`)
    }

  }
}
</script>

